/* global Office, Word */

const CONFIG = {
  correoRevisor: "practica.sis4@spradling.group",
  correoFirmante: "practica.sis4@spradling.group",
  urlPowerAutomate: ""
};

let usuarioActual = "";
let firmaCargada = null;
let firmaSeleccionadaBase64 = null;

// ============================================================
// INICIALIZACI√ìN
// ============================================================
function iniciarApp() {
  try {
    obtenerUsuarioActual();
    configurarInterfaz();
    configurarEventos();
    cargarFirmaGuardada();

    document.getElementById("loading").style.display = "none";
    document.getElementById("contenido").style.display = "block";

  } catch (error) {
    document.getElementById("loading").innerHTML = "<p style='color:red'>Error: " + error.message + "</p>";
  }
}

if (document.readyState === "complete" || document.readyState === "interactive") {
  setTimeout(iniciarApp, 500);
} else {
  document.addEventListener("DOMContentLoaded", iniciarApp);
}

// ============================================================
// OBTENER USUARIO ACTUAL
// ============================================================
function obtenerUsuarioActual() {
  try {
    usuarioActual = "practica.sis4@spradling.group";
    document.getElementById("nombre-usuario").textContent = usuarioActual;
  } catch (e) {
    usuarioActual = "practica.sis4@spradling.group";
    document.getElementById("nombre-usuario").textContent = usuarioActual;
  }
}

// ============================================================
// CONFIGURAR INTERFAZ SEG√öN USUARIO
// ============================================================
function configurarInterfaz() {
  const seccionEnviar = document.getElementById("seccion-enviar");
  const seccionFirmar = document.getElementById("seccion-firmar");

  const esRevisor = usuarioActual.toLowerCase() === CONFIG.correoRevisor.toLowerCase();
  const esFirmante = usuarioActual.toLowerCase() === CONFIG.correoFirmante.toLowerCase();

  if (esRevisor) {
    seccionEnviar.style.display = "block";
  }

  if (esFirmante) {
    seccionFirmar.style.display = "block";
  }

  if (!esRevisor && !esFirmante) {
    mostrarMensaje("No tienes permisos para usar este Add-in.", "error");
  }
}

// ============================================================
// CONFIGURAR EVENTOS
// ============================================================
function configurarEventos() {
  document.getElementById("btn-enviar").addEventListener("click", enviarParaAprobacion);
  document.getElementById("btn-firmar").addEventListener("click", iniciarFirma);
  document.getElementById("btn-seleccionar-archivo").addEventListener("click", () => {
    document.getElementById("input-firma").click();
  });
  document.getElementById("input-firma").addEventListener("change", manejarArchivoSeleccionado);
  document.getElementById("btn-confirmar-firma").addEventListener("click", confirmarYFirmar);
  document.getElementById("btn-cambiar-firma").addEventListener("click", cambiarFirma);
}

// ============================================================
// ENVIAR PARA APROBACI√ìN
// ============================================================
async function enviarParaAprobacion() {
  try {
    mostrarMensaje("Enviando correo de aprobaci√≥n...", "info");
    document.getElementById("btn-enviar").disabled = true;

    let nombreDocumento = "Documento sin nombre";
    let urlDocumento = "URL no disponible";

    try {
      if (typeof Office !== "undefined" && Office.context && Office.context.document) {
        if (Office.context.document.url) {
          nombreDocumento = decodeURIComponent(
            Office.context.document.url.split("/").pop().split("?")[0]
          );
          urlDocumento = Office.context.document.url;
        }
      }
    } catch (e) {}

    if (!CONFIG.urlPowerAutomate) {
      mostrarMensaje("‚úÖ [PRUEBA] Correo enviado a: " + CONFIG.correoFirmante + " | Documento: " + nombreDocumento, "exito");
      document.getElementById("btn-enviar").disabled = false;
      return;
    }

    const respuesta = await fetch(CONFIG.urlPowerAutomate, {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({
        accion: "enviarAprobacion",
        nombreDocumento: nombreDocumento,
        urlDocumento: urlDocumento,
        correoFirmante: CONFIG.correoFirmante,
        correoRevisor: CONFIG.correoRevisor
      })
    });

    if (respuesta.ok) {
      mostrarMensaje("‚úÖ Correo enviado a " + CONFIG.correoFirmante, "exito");
    } else {
      throw new Error("Error al enviar el correo");
    }

  } catch (error) {
    mostrarMensaje("‚ùå Error: " + error.message, "error");
  } finally {
    document.getElementById("btn-enviar").disabled = false;
  }
}

// ============================================================
// INICIAR FIRMA
// ============================================================
async function iniciarFirma() {
  if (firmaCargada && firmaCargada.length > 100) {
    // Segunda vez: aplicar firma directamente
    await aplicarFirmaAlDocumento(firmaCargada);
  } else {
    // Primera vez: mostrar panel para subir imagen
    firmaCargada = null;
    document.getElementById("panel-firma").style.display = "block";
    document.getElementById("btn-firmar").style.display = "none";
    document.getElementById("btn-seleccionar-archivo").style.display = "block";
    document.getElementById("preview-firma").style.display = "none";
    document.getElementById("mensaje").style.display = "none";
  }
}

// ============================================================
// MANEJAR ARCHIVO SELECCIONADO
// ============================================================
function manejarArchivoSeleccionado(e) {
  const archivo = e.target.files[0];

  if (!archivo) return;

  if (!archivo.type.startsWith("image/")) {
    mostrarMensaje("‚ùå Por favor selecciona un archivo de imagen (PNG, JPG, GIF).", "error");
    return;
  }

  if (archivo.size > 2 * 1024 * 1024) {
    mostrarMensaje("‚ùå La imagen es muy grande. M√°ximo 2MB.", "error");
    return;
  }

  const reader = new FileReader();
  reader.onload = function (event) {
    firmaSeleccionadaBase64 = event.target.result;
    document.getElementById("img-preview").src = firmaSeleccionadaBase64;
    document.getElementById("preview-firma").style.display = "block";
    document.getElementById("btn-seleccionar-archivo").style.display = "none";
    mostrarMensaje("Imagen cargada. Confirma para firmar el documento.", "info");
  };

  reader.readAsDataURL(archivo);
}

// ============================================================
// CONFIRMAR Y FIRMAR
// ============================================================
async function confirmarYFirmar() {
  if (!firmaSeleccionadaBase64) {
    mostrarMensaje("‚ùå No hay imagen de firma seleccionada.", "error");
    return;
  }

  try {
    // Guardar en localStorage
    try {
      localStorage.setItem("firma_usuario", firmaSeleccionadaBase64);
    } catch (e) {}

    // Guardar en Office settings si est√° disponible
    try {
      if (typeof Office !== "undefined" && Office.context && Office.context.document) {
        Office.context.document.settings.set("firma_usuario", firmaSeleccionadaBase64);
        await guardarSettings();
      }
    } catch (e) {
      console.log("Office settings no disponible, usando localStorage");
    }

    firmaCargada = firmaSeleccionadaBase64;
    await aplicarFirmaAlDocumento(firmaCargada);

  } catch (error) {
    mostrarMensaje("‚ùå Error al confirmar firma: " + error.message, "error");
  }
}

// ============================================================
// CAMBIAR FIRMA
// ============================================================
function cambiarFirma() {
  firmaSeleccionadaBase64 = null;
  document.getElementById("preview-firma").style.display = "none";
  document.getElementById("btn-seleccionar-archivo").style.display = "block";
  document.getElementById("input-firma").value = "";
  document.getElementById("img-preview").src = "";
  document.getElementById("mensaje").style.display = "none";
}

// ============================================================
// APLICAR FIRMA AL DOCUMENTO
// ============================================================
async function aplicarFirmaAlDocumento(firmaBase64) {
  try {
    mostrarMensaje("Aplicando firma al documento...", "info");

    if (typeof Word === "undefined") {
      document.getElementById("panel-firma").style.display = "block";
      document.getElementById("btn-seleccionar-archivo").style.display = "none";
      document.getElementById("preview-firma").style.display = "block";
      document.getElementById("img-preview").src = firmaBase64;
      document.getElementById("btn-confirmar-firma").style.display = "none";
      document.getElementById("btn-cambiar-firma").style.display = "none";
      document.getElementById("btn-firmar").style.display = "none";
      mostrarMensaje("‚úÖ [PRUEBA] Firma lista. Al desplegar en SharePoint se insertar√° en Word.", "exito");
      return;
    }

    await Word.run(async (context) => {
      const cuerpo = context.document.body;

      cuerpo.insertBreak(Word.BreakType.pageSectionContinuous, Word.InsertLocation.end);

      const parrafoTitulo = cuerpo.insertParagraph("Firma", Word.InsertLocation.end);
      parrafoTitulo.styleBuiltIn = Word.Style.heading1;
      parrafoTitulo.alignment = Word.Alignment.centered;

      const parrafoFirma = cuerpo.insertParagraph("", Word.InsertLocation.end);
      parrafoFirma.alignment = Word.Alignment.centered;

      const base64Data = firmaBase64.replace(/^data:image\/(png|jpeg|gif);base64,/, "");
      parrafoFirma.insertInlinePictureFromBase64(base64Data, Word.InsertLocation.end);

      const parrafoInfo = cuerpo.insertParagraph(
        "Firmado por: " + usuarioActual + " | Fecha: " + new Date().toLocaleDateString("es-ES"),
        Word.InsertLocation.end
      );
      parrafoInfo.alignment = Word.Alignment.centered;
      parrafoInfo.font.size = 10;
      parrafoInfo.font.color = "#666666";

      await context.sync();
    });

    document.getElementById("panel-firma").style.display = "none";
    document.getElementById("btn-firmar").style.display = "none";
    mostrarMensaje("‚úÖ Documento firmado exitosamente. Guardando PDF...", "exito");
    await guardarComoPDF();

  } catch (error) {
    mostrarMensaje("‚ùå Error al aplicar la firma: " + error.message, "error");
  }
}

// ============================================================
// GUARDAR COMO PDF
// ============================================================
async function guardarComoPDF() {
  try {
    let nombreDocumento = "Documento";
    let urlDocumento = "";

    try {
      if (typeof Office !== "undefined" && Office.context && Office.context.document) {
        if (Office.context.document.url) {
          nombreDocumento = decodeURIComponent(
            Office.context.document.url.split("/").pop().split("?")[0]
          ).replace(/\.[^/.]+$/, "");
          urlDocumento = Office.context.document.url;
        }
      }
    } catch (e) {}

    if (!CONFIG.urlPowerAutomate) {
      mostrarMensaje("‚úÖ [PRUEBA] PDF se guardar√≠a como: '" + nombreDocumento + " firmado.pdf' en SharePoint.", "exito");
      return;
    }

    const respuesta = await fetch(CONFIG.urlPowerAutomate, {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({
        accion: "guardarPDF",
        nombreDocumento: nombreDocumento,
        urlDocumento: urlDocumento,
        correoFirmante: CONFIG.correoFirmante
      })
    });

    if (respuesta.ok) {
      mostrarMensaje("‚úÖ PDF guardado en SharePoint.", "exito");
    } else {
      throw new Error("Error al guardar el PDF");
    }

  } catch (error) {
    mostrarMensaje("‚ùå Error al guardar PDF: " + error.message, "error");
  }
}

// ============================================================
// CARGAR FIRMA GUARDADA
// ============================================================
function cargarFirmaGuardada() {
  try {
    const firmaLocal = localStorage.getItem("firma_usuario");
    if (firmaLocal && firmaLocal.length > 100) {
      firmaCargada = firmaLocal;
      console.log("Firma cargada desde localStorage");
      return;
    }

    if (typeof Office !== "undefined" && Office.context && Office.context.document) {
      Office.context.document.settings.refreshAsync((result) => {
        if (result.status === Office.AsyncResultStatus.Succeeded) {
          const firmaGuardada = Office.context.document.settings.get("firma_usuario");
          if (firmaGuardada) {
            firmaCargada = firmaGuardada;
          }
        }
      });
    }
  } catch (e) {
    console.log("No hay firma guardada previamente");
  }
}

// ============================================================
// GUARDAR SETTINGS
// ============================================================
function guardarSettings() {
  return new Promise((resolve, reject) => {
    Office.context.document.settings.saveAsync((result) => {
      if (result.status === Office.AsyncResultStatus.Succeeded) {
        resolve();
      } else {
        reject(new Error("Error al guardar configuraci√≥n"));
      }
    });
  });
}

// ============================================================
// MOSTRAR MENSAJES
// ============================================================
function mostrarMensaje(texto, tipo) {
  const div = document.getElementById("mensaje");
  div.textContent = texto;
  div.className = tipo;
  div.style.display = "block";
}